import React, { useRef, useEffect, useState } from 'react';
import * as d3 from 'd3';
import { withStreamlitConnection, Streamlit } from 'streamlit-component-lib';
import { Helmet } from 'react-helmet';
import { BaseType,  } from 'd3';

// Define a type for the node structure based on your provided attributes
interface Node {
  id: number;
  name: string;
  label?: string;
  color?: string;
  return_value?: any;
  disabled?: boolean;
  icon?: string;
  icon_style?: string;
  expand_direction?: 'up' | 'down';
  children?: Node[];
  size?: number;
  label_position?: 'bottom' | 'top' | 'left' | 'right';
  label_style?: string;
  node_style?: string;
  center?: { x: number; y: number }; 
  tooltip?: string;

}
// Define a type for your component props
interface CustomerJourneyProps {
  args: {
    content: Node[];
    space_main_nodes: number;
    space_between_child_nodes: number;
    custom_font_awesome_url: string;
    height_user: number;
    max_width: number | string;
    first_child_extra_space: number;
    mainLabelOffset?: number;
    custom_css?: string;
    tooltipStyle: React.CSSProperties;
    mainNodeHoverStyle: React.CSSProperties;
    mainNodeClickedStyle: React.CSSProperties;
    mainNodeDefaultStyle: React.CSSProperties;
    childNodeHoverStyle: React.CSSProperties;
    childNodeClickedStyle: React.CSSProperties;
    childNodeDefaultStyle: React.CSSProperties;
    mainNodeLineStyle: React.CSSProperties;
    ChildNodeLineStyle: React.CSSProperties;

  };
}

const CustomerJourneyComponent = (props: CustomerJourneyProps) => {
  const { content, space_main_nodes, space_between_child_nodes, custom_font_awesome_url,height_user,max_width, first_child_extra_space,
     mainLabelOffset, custom_css, tooltipStyle, mainNodeHoverStyle, mainNodeClickedStyle, mainNodeDefaultStyle, childNodeHoverStyle,
     childNodeClickedStyle, childNodeDefaultStyle, mainNodeLineStyle} = props.args;

  const ref = useRef<SVGSVGElement>(null);
  const [svgHeight, setSvgHeight] = useState(500);
  let currentlyClickedNode: d3.Selection<SVGCircleElement, Node, BaseType, unknown> | null = null;
  const [clickedNodeId, setClickedNodeId] = useState<number | null>(null);



  useEffect(() => {
    if (!content || !ref.current) {
      return;
    }

    const maxChildCount = getMaxChildCount(content);
    const mainNodeSpacing = space_main_nodes;
    const childNodeOffset = space_between_child_nodes;
    const margin = { top: 25, right: 25, bottom: 50, left: 40 };

    const width = ref.current.parentElement?.offsetWidth || 800;
    const height = calculateHeight(content, childNodeOffset, maxChildCount, margin);

    setSvgHeight(height + margin.top + margin.bottom);

    d3.select(ref.current).selectAll("*").remove();

    // Remove the margin.top from the translation to reduce the white space above the content
    const svgGroupTransform = `translate(${margin.left},0)`; // Changed from ${margin.top} to 0

    const svg = d3.select(ref.current)
      .attr("class", "svg-customer-journey")
      .append("svg")
      .attr("width", max_width)
      .attr("height", svgHeight) // You might need to adjust this height based on your content
      .append("g")
      .attr("transform", svgGroupTransform);

    // Drawing lines between main nodes and their children
    content.forEach((node, i) => {
      const mainNodeX = i * mainNodeSpacing;
      const mainNodeY = height / 2;
      node.center = { x: mainNodeX, y: mainNodeY };

      if (node.children && node.children.length > 0) {
        node.children.forEach((child, childIndex) => {
          const childNodeSize = child.size || 10;
          const childPos = calculateChildPosition(mainNodeX, mainNodeY, node.expand_direction || 'up', childNodeOffset, childIndex, childNodeSize, node.children?.length ?? 0, first_child_extra_space );
          child.center = childPos;

          svg.append("line")
            .attr("class", "line_betweend_main_and_child_nodes")
            .attr("x1", mainNodeX)
            .attr("y1", mainNodeY)
            .attr("x2", child.center.x)
            .attr("y2", child.center.y)
            .attr("stroke", "black")
            .attr("stroke-width", 2)
            .lower();
        });
      }

      // Draw lines between main nodes
      if (i < content.length - 1) {
        const nextNode = content[i + 1];
        const startX = mainNodeX + (node.size || 10) / 2;
        const endX = (i + 1) * mainNodeSpacing - (nextNode.size || 10) / 2;

        svg.append("line")
          .attr("class", "line_between_main_nodes")
          .attr("x1", startX)
          .attr("y1", mainNodeY)
          .attr("x2", endX)
          .attr("y2", mainNodeY)
          .attr("stroke", "black")
          .attr("stroke-width", 2)
          .lower();
      }
    });

     // Then append nodes
  content.forEach((node, i) => {
    
    const mainNodeX = i * mainNodeSpacing;
    const mainNodeY = height / 2;
    // Store the center point for each node
    node.center = { x: mainNodeX, y: mainNodeY };

    function applyClickedStyle(nodeElement: d3.Selection<SVGCircleElement, Node, BaseType, unknown>, nodeId: number) {
      setClickedNodeId(nodeId)
      currentlyClickedNode = nodeElement;
    }
    

    function showTooltip(event: MouseEvent, d: unknown, nodeElement: d3.Selection<SVGCircleElement, Node, BaseType, unknown>) {
      const data = d as Node;
      if (data.tooltip) {
        const tooltipDiv = d3.select('#node-tooltip');
        tooltipDiv
          .html(data.tooltip) // assuming d.tooltip contains the HTML content for the tooltip
          .style('visibility', 'visible')
          .style('left', (event.pageX + 10) + 'px')
          .style('top', (event.pageY + 10) + 'px');
      }
    }

    function hideTooltip() {
      const tooltipDiv = d3.select('#node-tooltip');
      tooltipDiv.style('visibility', 'hidden');
    }

    const  duration_ms = 200;

    function handleMouseOver(event: MouseEvent, d: unknown, nodeElement: d3.Selection<SVGCircleElement, Node, BaseType, unknown>) {
      const data = d as Node;
      if (data.id !== clickedNodeId) {
      nodeElement
        .transition()
        .duration(duration_ms)
        .attr("r", (data.size || 10) * 1.1)
        .style("stroke", "black")
        .style("stroke-width", "2px");
    }
   
    }
    
    function handleMouseOut(event: MouseEvent, d: unknown, nodeElement: d3.Selection<SVGCircleElement, Node, BaseType, unknown>) {
      const data = d as Node;
      // Only reset the style if this node is not the currently clicked node
      if (data.id !== clickedNodeId) {
        nodeElement
          .transition()
          .duration(duration_ms)
          .attr("r", data.size || 10)
          .style("stroke", null)
          .style("stroke-width", null);
      }
    }

    const defs = svg.append("defs");

      // Create drop shadow filter
  const dropShadowFilter = defs.append("filter")
  .attr("id", "drop-shadow")
  .attr("height", "130%");

  // Append Gaussian blur
  dropShadowFilter.append("feGaussianBlur")
  .attr("in", "SourceAlpha")
  .attr("stdDeviation", 3)
  .attr("result", "blur");

  // Append offset
  dropShadowFilter.append("feOffset")
  .attr("in", "blur")
  .attr("dx", 2)
  .attr("dy", 2)
  .attr("result", "offsetBlur");

  // Merge effects
  dropShadowFilter.append("feMerge")
  .call(merge => {
    merge.append("feMergeNode").attr("in", "offsetBlur");
    merge.append("feMergeNode").attr("in", "SourceGraphic");
  });


  const mainNodeGroup = svg.append("g")
    .attr("class", "main-node-group")
    .attr("id", `main-node-group-${node.id}`)
    .attr("transform", `translate(${mainNodeX},${mainNodeY})`);

  // Append main node circle
  const mainNodeCircle = mainNodeGroup.append("circle")
  .attr("id", `main-node-${node.id}`)
  .attr("class", "main-node")
  .attr("r", node.size ? (node.id === clickedNodeId ? node.size * 1.05 : node.size) : 10)
  .attr("fill",  (node.color || 'gray'))
  .attr("style", node.id === clickedNodeId ? 'stroke: black; stroke-width: 2px;' : (node.node_style || ''))
  .attr("filter", node.id === clickedNodeId ? 'url(#drop-shadow)' : null)
  .datum(node);

  // Add click event handler
  mainNodeCircle.on('click', () => {
    if (!node.disabled) {
      setClickedNodeId(node.id);
      Streamlit.setComponentValue({node: node, return_value: node.return_value, id: node.id});
    }
  });



  // Add hover effects and cursor style
  if (!node.disabled) {
    mainNodeCircle.style("cursor", "pointer");

    mainNodeCircle.on('mouseover', function(event: MouseEvent, d: unknown) {

      handleMouseOver(event, d, mainNodeCircle);
      showTooltip(event, d, mainNodeCircle)

      // const styles = mainNodeHoverStyle;
      // if (styles)
      //   for (const [key, value] of Object.entries(styles)) {
      //     d3.select(this).style(key, value);
      //   }
    });
    
    mainNodeCircle.on('mouseout', function(event: MouseEvent, d: unknown) {
      handleMouseOut(event, d, mainNodeCircle);
      hideTooltip();

    });
  }

      // Main node FontAwesome icon
    if (node.icon) {

      const  mainNodeIcon = mainNodeGroup.append("foreignObject")
        .attr("id", `main-node-icon-${node.id}`)
        .attr("width", node.size || 10)
        .attr("height", node.size || 10)
        .attr("x", -(node.size || 10) / 2)
        .attr("y", -(node.size || 10) / 2)
        .append("xhtml:div")
        .attr("class", "icon-container main-node-icon")
        .style("display", "flex")
        .style("align-items", "center")
        .style("justify-content", "center")
        .style("height", `${node.size || 10 * 2}px`) // Set the height of the container
        .style("line-height", `${node.size || 10 * 2}px`) // Set the line-height to match the height for vertical centering    
        .html(`<i class="${node.icon}" style="${node.icon_style}"></i>`)
        .datum(node);

        //Wir m端ssen das On click event auch dem Icon hinzuf端gen ansonsten werden Klicke auf das Icon nicht erkannt
        if (!node.disabled) {
          mainNodeIcon.style("cursor", "pointer");
          mainNodeIcon.on('click', () => {
            // Return the node data to Streamlit
            applyClickedStyle(mainNodeCircle, node.id); // Apply the clicked style to the main node circle
            Streamlit.setComponentValue({node: node, return_value: node.return_value, id: node.id});
          });

          mainNodeIcon.on('mouseover', function(event: MouseEvent, d: unknown) {
            showTooltip(event, d, mainNodeCircle)
            handleMouseOver(event, d, mainNodeCircle);

            // const styles = mainNodeHoverStyle;
            // if (styles)
            //   for (const [key, value] of Object.entries(styles)) {
            //     d3.select(this).style(key, value);
            //   }
          });
          // });

          mainNodeIcon.on('mouseout', function(event: MouseEvent, d: unknown) {
            hideTooltip();
            handleMouseOut(event, d, mainNodeCircle);
          });


        }
    }

    // Label main Node Group
    if (node.label) {
        const labelYOffset = mainLabelOffset || 40;

        const tempText = svg.append("text")
        .attr("class", "main-node-label")
        .attr("id", `main-node-label-${node.id}`) // Add an id to the text element so we can select it later for calculating the width
        .attr('visibility', 'hidden') // Hide the temporary text element
        .attr("style", node.label_style || '')
        .text(node.label);

      // Get the width of the text from the temporary text element
      const labelWidth = tempText.node()!.getComputedTextLength();
  
      // Remove the temporary text element from the DOM
      tempText.remove();
  
        let labelX = 0;
        let labelY = 0;

        const labelXOffset = labelWidth / 2;
  
        switch (node.label_position) {
          case 'top':
            labelX = -labelXOffset;
            labelY = -labelYOffset;
            break;
          case 'bottom':
            labelY = labelYOffset;
            labelX = -labelXOffset;
            break;
          case 'left':
            labelX = -labelXOffset;
            break;
          case 'right':
            labelX = labelXOffset;
            break;
          default:
            labelX = 0;
            labelY = labelYOffset;
            break;
        }
  
        mainNodeGroup.append("text")
          .attr("class", "main-node-label")
          .attr("id", `main-node-label-${node.id}`)
          .attr("x", labelX)
          .attr("y", labelY)
          .attr("style", node.label_style || '')
          .html(node.label);
      }
    
    // Append children nodes
    if (node.children && node.children.length > 0) {
      node.children.forEach((child, childIndex) => {
        // Calculate position for child node
        const childPos = calculateChildPosition(mainNodeX, mainNodeY, node.expand_direction || 'up', childNodeOffset, childIndex, child.size || 10, node.children?.length ?? 0, first_child_extra_space);
        child.center = childPos;

        const childNodeGroup = svg.append("g")
        .attr("id", `child-node-group-${child.id}`)
        .attr("class", "child-node-group")
        .attr("transform", `translate(${childPos.x},${childPos.y})`);

  

        // Append child node circle
        const childNodeCircle = childNodeGroup.append("circle")
        .attr("id", `child-node-${child.id}`)
        .attr("class", "child-node")
        .attr("r", child.size ? (child.id === clickedNodeId ? child.size * 1.1 : child.size) : 5)
          .attr("fill", child.color || 'gray')
          .attr("style", child.id === clickedNodeId ? 'stroke: black; stroke-width: 2px;' : (child.node_style || ''))
          // .attr("filter", child.id === clickedNodeId ? 'url(#drop-shadow)' : null)
          .datum(child);


    
          childNodeCircle.on('click', () => {
            // Check if node is not disabled
            if (!child.disabled) {
              // Return the node data to Streamlit
              applyClickedStyle(childNodeCircle, child.id); // Apply the clicked style to the child node circle
              Streamlit.setComponentValue({node: node, return_value: child.return_value, id: child.id});
            }
          });

        // Change cursor if the node is not disabled
        if (!child.disabled) {
            childNodeCircle.style("cursor", "pointer");
              // Attach mouseover and mouseout events to child nodes for tooltip
      childNodeCircle.on('mouseover', function(event, d) {
        showTooltip(event, d, childNodeCircle);
        handleMouseOver(event, d, childNodeCircle);
      });
      childNodeCircle.on('mouseout', function(event, d) {
        hideTooltip();
        handleMouseOut(event, d, childNodeCircle);
      });
    }
    else {
      childNodeCircle.on('mouseover', function(event, d) {
        showTooltip(event, d, childNodeCircle);
      });
      childNodeCircle.on('mouseout', function(event, d) {
        hideTooltip();
      }
      );
    }

        // Child node FontAwesome icon
        
      // Child node FontAwesome icon
    if (child.icon) {

      childNodeGroup.append("foreignObject")
        .attr("id", `child-node-icon-${child.id}`)
        .attr("width", child.size || 5)
        .attr("height", child.size || 5)
        .attr("x", -(child.size || 5) / 2)
        .attr("y", -(child.size || 5) / 2)
        .append("xhtml:div")
        .attr("class", "icon-container child-node-icon-container")
        .style("display", "flex")
        .style("align-items", "center")
        .style("justify-content", "center")
        .style("height", `${child.size || 5 * 2}px`) // Set the height of the container
        .style("line-height", `${child.size || 5 * 2}px`) // Set the line-height to match the height for vertical centering    
        .html(`<i class="${child.icon}" style="${child.icon_style}"></i>`)
        .datum(child);

        //Wir m端ssen das On click event auch dem Icon hinzuf端gen ansonsten werden Klicke auf das Icon nicht erkannt
        if (!child.disabled) {
          childNodeCircle.style("cursor", "pointer");
          childNodeCircle.on('click', () => {
            // Return the node data to Streamlit
            applyClickedStyle(childNodeCircle, child.id); // Apply the clicked style to the child node circle
            Streamlit.setComponentValue({node: node, return_value: child.return_value, id: child.id});
          });

          childNodeCircle.on('mouseover', function(event: MouseEvent, d: unknown) {
            // console.log("hover")
            // showTooltip(event, d, childNodeCircle)
            handleMouseOver(event, d, childNodeCircle);
          });

          childNodeCircle.on('mouseout', function(event: MouseEvent, d: unknown) {
            handleMouseOut(event, d, childNodeCircle);
            // hideTooltip();
          });

    }
  }
        
        // Label child Node Group
        if (child.label) {
          childNodeGroup.append("text")
            .attr("class", "child-node-label")
            .attr("id", `child-node-label-${child.id}`)
            .attr("x", child.label_position === 'right' ? 15 : -100)
            .attr("y", 5)
            .attr("style", child.label_style || '')
            .html(child.label);
        }
      });
    }
  });

  

      // Update the setFrameHeight call to the new svgHeight that you calculate based on the content
  Streamlit.setFrameHeight(height_user);

  }, [content, space_main_nodes, space_between_child_nodes]);

// // If you decide to use a fixed height
// const fixedHeight = 400; // Replace with the height that fits your content

// useEffect(() => {
//   setSvgHeight(fixedHeight); // Set the fixed height when the component mounts
//   Streamlit.setFrameHeight(fixedHeight); // Update the Streamlit frame height
// }, []);


  const calculateHeight = (nodes: Node[], childNodeOffset: number, maxChildCount: number, margin: {top: number; right: number; bottom: number; left: number}): number => {
    // Calculate the total height required for the svg based on the nodes and their children
    // This is a placeholder calculation and you might need to adjust the logic to fit your content
    let height = 200; // nodeSize should be the size of your nodes, margin.betweenNodes is a made-up value for spacing between nodes
    const additionalSpaceForChildren = maxChildCount * childNodeOffset;
    return height + additionalSpaceForChildren;
  };


  // Calculate maximum child count among all nodes
  const getMaxChildCount = (nodes: Node[]): number => {
    return Math.max(...nodes.map(node => node.children ? node.children.length : 0));
  };

  const calculateChildPosition = (parentX: number, parentY: number, expandDirection: 'up' | 'down', childNodeOffset: number, index: number, childSize: number, totalChildren: number, firstChildExtraSpace: number) => {
    // Calculate the base offset direction (positive for 'down', negative for 'up')
    const directionMultiplier = expandDirection === 'up' ? -1 : 1;
  
    // Start with the base offset for child nodes
    var yOffset = directionMultiplier * childNodeOffset * index;
  
    // Increase the yOffset by the size of the node to avoid overlapping
    yOffset += directionMultiplier * childSize / 2;
  
    // If it's the first child, add the extra space to the yOffset
    if (index === 0) {
      yOffset += directionMultiplier * firstChildExtraSpace;
    } else {
      // For subsequent children, ensure they are spaced out by including the first child's extra space in their offset
      yOffset += directionMultiplier * firstChildExtraSpace// / (totalChildren - 1);
    }
  
    return {
      x: parentX,
      y: parentY + yOffset
    };
  };


return (
  <>
   {/* Add css string from user input */}
    <style>
      {custom_css}
    </style>
    <Helmet>
      <script
        src={custom_font_awesome_url}
        crossOrigin="anonymous"
        id="font-awesome-icons"
      />
    </Helmet>
    <div style={{ overflowX: 'auto', width: '100%'}}>
      {/* Ensure that the SVG has a minWidth that allows for scrolling */}
      {/* The minWidth must be set to a value that will accommodate all content */}
      {/* For example, if the furthest element is at 2000px, you would set the minWidth accordingly */}
      <svg ref={ref} style={{ height: `${svgHeight}px`, minWidth: max_width }} />
    </div>

    {/* Tooltip div */}
    <div id="node-tooltip" style={{
    ...{ // Default tooltip styles
      position: 'absolute',
      visibility: 'hidden',
      padding: '10px',
      background: 'rgba(255, 255, 255, 0.9)',
      border: '1.5px solid #ddd',
      borderRadius: '4px',
      pointerEvents: 'none',
      zIndex: 10,
    },
    ...tooltipStyle // Apply custom styles
  }}></div>
  </>
);


};

export default withStreamlitConnection(CustomerJourneyComponent);